}

/* Info Cards */
.dashboard .info-card {
  padding-bottom: 10px;
  padding-top: 10px;
}

.dashboard .info-card h6 {
  font-size: 28px;
  color: white;
  font-weight: 700;
  margin: 0;
  padding: 0;
}

.dashboard .card-icon {
  font-size: 32px;
  line-height: 0;
  width: 64px;
  height: 64px;
  flex-shrink: 0;
  flex-grow: 0;
